import React,{useState,useEffect} from "react";
import { Link, useNavigate} from "react-router-dom";
import M from "materialize-css";

const Signup = () =>{

    const history=useNavigate()
    const [name,setName]=useState("")
    const [password,setPassword]=useState("")
    const [email,setEmail]=useState("")
    const [image,setImage]=useState("")
    const [url,setUrl]=useState(undefined)

    useEffect(()=>{
        if(url){
            uploadField()
        }
    },[url])

    const uploadPic = () => {
        const data=new FormData()
        data.append("file",image)
        data.append("upload_preset","Destishare")
        data.append("cloud_name","dszvlltey")
        fetch("https://api.cloudinary.com/v1_1/dszvlltey/image/upload",{
            method:"post",
            body:data
        })
        .then(res=>res.json())
        .then(data=>{
                setUrl(data.url)
        })
        .catch(err=>{
            console.log(err)
        })

    }

    const uploadField = () => {
        if(!/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(email)){
            return M.toast({html:"invalid email",classes:"#e53935 red darken-1"})
        }
        fetch("/signup",{
            method:"post",
            headers:{
                "Content-Type":"application/json"
            },
            body:JSON.stringify({
                name,
                password,
                email,
                pic:url
            })
        }).then(res=>res.json())
        .then(data=>{
            if(data.error){
                M.toast({html:data.error,classes:"#e53935 red darken-1"})
            }else{
                M.toast({html:data.message,classes:"#43a047 green darken-1"})
                history("/login")
            }
        }).catch(err=>{
            console.log(err)
        })  
    }

     const PostData=()=>{
        if(image){
            uploadPic()
        }else{
            uploadField()
        }
        
    }

    return(
        <div className="Mycard">
            <div className="card auth-card input-field">
                <h2>DestiShare</h2>
                < input
                type="text"
                placeholder="name"
                value={name}
                onChange={(e)=>setName(e.target.value)}
                />
                < input
                type="text"
                placeholder="email"
                value={email}
                onChange={(e)=>setEmail(e.target.value)}
                />
                < input
                type="password"
                placeholder="password"
                value={password}
                onChange={(e)=>setPassword(e.target.value)}
                />
                <div className="file-field input-field">
                    <div className="btn #7986cb indigo lighten-2">
                        <span>Upload pic</span>
                        <input type="file" onChange={(e)=>setImage(e.target.files[0])}/>
                    </div>
                    <div className="file-path-wrapper">
                        <input className="file-path validate" type="text" placeholder="Upload one or more files" />
                    </div>
                </div>


                <button className="btn waves-effect waves-light #7986cb indigo lighten-2"
                    onClick={()=>PostData()}
                >
                    Signup
                </button>
                        
                <h5>
                    <Link to="/login">Already have an account?</Link>
                </h5>
            </div>
        </div>
    );
}

export default Signup